/* 
0 - 600px:    Phone
600 - 900px:  Tablet portrait
900 - 1200px: Tablet landscape
1200 - 1800p
1800px + :    Big desktop

//Breakpoints
-phone
-tab-port
-tab-land
-big-desktop
*/
@import url("https://fonts.googleapis.com/css2?family=Overpass:wght@300;600&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Ubuntu:wght@400;500;700&display=swap");
*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
}
@media (max-width: 37.5em) {
  html {
    font-size: 50%;
  }
}
@media (min-width: 37.5625em) and (max-width: 56.25em) {
  html {
    font-size: 50%;
  }
}
@media (min-width: 56.3125em) and (max-width: 75em) {
  html {
    font-size: 56.25%;
  }
}
@media (min-width: 112.5em) {
  html {
    font-size: 75%;
  }
}

a {
  text-decoration: none;
  color: inherit;
}

ul {
  list-style: none;
}

body {
  box-sizing: border-box;
}

body {
  font-family: "Ubuntu", sans-serif;
}

.container {
  min-height: 90vh;
}

.header {
  background: #ff525d;
  background: linear-gradient(90deg, #ff8f70 10%, #ff525d 70%);
  position: relative;
  min-height: 80vh;
  overflow: hidden;
  justify-content: space-evenly;
  border-bottom-left-radius: 70px;
}
.header img {
  position: absolute;
  display: block;
  max-width: 150%;
  top: -140%;
  left: -20%;
}
.header-nav {
  display: flex;
  align-items: center;
  justify-content: space-around;
  height: 15vh;
  font-size: 1.6rem;
  color: white;
}
.header-nav-nav {
  z-index: 999;
  display: flex;
}
.header-nav-nav #logo {
  margin-right: 8rem;
}
.header-nav-nav ul {
  display: flex;
}
.header-nav-nav ul li {
  margin-right: 6rem;
  position: relative;
}
.header-nav-nav ul li:after {
  content: "";
  position: absolute;
  top: 5%;
  margin: 0.4rem;
  width: 1.5rem;
  height: 1.5rem;
  background-image: url(/images/icon-arrow-light.svg);
  background-size: contain;
  background-repeat: no-repeat;
}
.header-nav-nav ul li:last-child {
  margin-right: 0rem;
}
.header-nav-user {
  z-index: 999;
  display: flex;
}
.header-nav-user a {
  margin-right: 6rem;
}
.header-nav-user a:last-child {
  margin-right: 0rem;
}
.header-main {
  z-index: 999;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;
}
.header-main h1 {
  font-size: 6rem;
  padding: 1rem;
}
.header-main p {
  font-size: 2.5rem;
  padding: 2rem;
}
.header-main-links {
  padding-top: 3rem;
}
.header-main-links a {
  margin-right: 5rem;
  font-size: 1.8rem;
}
.header-main-links a:last-child {
  margin-right: 0rem;
}

#whiteback {
  background-color: white;
  color: #ff525d;
  padding: 1.2rem 2rem;
  border-radius: 50px;
  font-weight: 600;
}

#borderedlink {
  padding: 1.2rem 2rem;
  border-radius: 50px;
  color: white;
  border: 1.5px solid white;
}

.future {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.future-content {
  display: flex;
  justify-content: center;
  align-items: center;
}
.future-left {
  padding: 6rem;
  font-size: 2rem;
  flex: 1;
}
.future-left article {
  margin: 5rem;
  line-height: 30px;
}
.future-left article h3 {
  color: #1f3f5b;
  padding: 1rem 0rem;
}
.future-left article p {
  color: #4b5862;
}
.future-right {
  flex: 1;
}
.future-right img {
  max-width: 100%;
  display: block;
  max-height: 130%;
}

.section-title {
  font-size: 2.1rem;
  margin-top: 5rem;
  color: #1f3f5b;
}

.state {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 60vh;
  background: #2c2d3f;
  color: white;
  background-image: url(/images/bg-pattern-circles.svg);
  background-repeat: no-repeat;
  background-size: 60%;
  background-position-y: bottom;
  margin-bottom: 4rem;
  border-top-right-radius: 90px;
  border-bottom-left-radius: 80px;
}
.state-phones {
  flex: 1;
}
.state-text {
  flex: 1;
  margin-right: 2rem 10rem 0rem 0rem;
  line-height: 30px;
}
.state-text h2 {
  font-size: 3rem;
  padding: 2rem;
}
.state-text p {
  font-size: 2rem;
  padding: 2rem;
}

.free {
  display: flex;
  justify-content: center;
  align-items: center;
}
.free-img {
  flex: 1;
}
.free-img img {
  display: block;
  max-width: 100%;
  float: left;
}
.free-txt {
  flex: 1;
}
.free-txt article {
  padding: 2rem;
  line-height: 26px;
}
.free-txt article h3 {
  font-size: 2.3rem;
  padding: 1.5rem 0rem;
  color: #1f3f5b;
}
.free-txt article p {
  font-size: 1.5rem;
  color: #4b5862;
  padding: 1.5rem 4rem;
}

.footer {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  background: #25252d;
  height: 40vh;
  border-top-right-radius: 80px;
}
.footer h2 {
  font-size: 3rem;
  color: white;
  display: flex;
  justify-self: flex-start;
  align-items: start;
  height: 60%;
}
.footer ul li {
  color: #c8c8cb;
  padding: 0.6rem;
  font-size: 1.2rem;
}
.footer ul li span {
  color: white;
  font-size: 1.4rem;
}